import React from "react";
import {Card, Collapse} from "react-bootstrap";

const HelpGraphCollapse = ({collapsed}) => (
    <Collapse in={collapsed}>
        <Card id="help-graph" className="text-justify">
            <Card.Body>
                <p>Vrcholy sa do grafu umiestuj tahanm alebo klikanm na ikonu z panelu. Pri ahan je
                    mon
                    uri presn polohu vrcholu, pri kliknut bude ma vrchol nhodn pozciu.
                    V grafe je mon umiestni 4 typy vrcholov.
                    Prv typ vrcholu predstavuje prvok domny.
                    Druh predstavuje kontantu.
                    Tret predstavuje ternrny (3) vzah.
                    tvrt predstavuje quaternrny (4) vzah.</p>

                <p>
                    Graf sa ovlda dvomi reimami. Pohybovac reim (<code>P</code>) dovouje presva alebo
                    maza jednotliv vrcholy. Pre ostatn akcie ako s pridvanie prediktov a funkci,
                    zmena
                    mien vrcholov alebo na vytvranie hrn sli Editovac reim (<code>E</code>).
                    Medzi tmito reimami sa d meni stlaenm prslunch tlaidiel alebo stlaenm
                    klves <code>P</code> a <code>E</code>. Okrem toho je mon vyui klvesu<code>;</code>,ktorou
                    je mon prepna medzi jednotlivmi reimami. Naddenm myou nad prvky grafu sa vm zobraz
                    npoveda k nm (resp. hlka preo nejde dan akcia vykona).</p>

                <p>
                    Dvojklikom na nzov vrcholu sa prepne nzov na input okno. Do tohto okna je mon zada
                    nov nzov.
                    Nov nzov mus spla podmienky, teda nesmie obsahova zakzan znaky. V prpade
                    pouitia zakzanch vzahov sa input okno rozsvieti na erveno.
                    Medzery v input okne s ignorovan a berie sa slovo beznich. Nzov sa akceptuje vybratm
                    inho prvky grafu alebo kliknutm do pltna. Okrem toho je mon meno akceptova aj
                    tlaidlom <code>ENTER</code>.
                    V prpade ak pouvate chce zrui premenovanie a vrti pvodn meno tak na to sli
                    tlaidlo <code>ESC</code>. To ist sa stane aj ked sa pouvate poksi uloi zl meno
                    - vrti sa pvodn meno.
                </p>

                <p>
                    Kontanta smie ma maximlne jeden uzol. Ak pouvate sa poksi pripoji novm uzlom
                    in vrchol predstavujci prvok domny tak sa pvodn uzol zmae. Uzol kontanty je mon
                    pripoji iba do vrcholu, ktor predstavuje prvok domny.
                </p>
                <p>
                    Miesto, kde sa nachdza nzov vrcholu sli na vytvranie a tahanie uzlov.
                    Binrne vzahy medzi dvomi vrcholmi sa vytvraj ahanm uzlu z jednho vrcholu do
                    druhho.
                    Binrn vzah do seba samho je mon vytvori ahanm uzlu z nzvu vrcholu do tlaidla
                    na
                    otvranie/zatvranie rozbaovacej ponuky.
                </p>

                <p>Graf nedovoluje prida funkciu do vrcholu ak by mal nasta stav, e funkcia by bola
                    viackrt definovan pre rovnak parametre. V takom prpade bu nejde vbec dan funkciu
                    prida a tlaidlo na pridanie je vypnut alebo ak ide o binrny vzah tak v om me
                    nasta
                    stav, e zaradenej funkci v zozname nejde zmeni smer
                    (resp. je mon vybera iba z obmedzenho potu smerov). tandardne sa v binrnom vzahu
                    pri
                    pridan prediktu alebo funkcie prid tento prvok v oboch smeroch. Ak v rmci funkcie
                    nie je
                    mon prida obidva smery, tak sa prid ten, ktor je dostupn. Pre ternrne a vie
                    vrcholy plat, e nejde vytvori dva takto vrcholy, ktor by mali rovnak kombinciu
                    parametrov. V takom prpade ak sa pouvate poksi spoji posledn uzol, tak tento uzol
                    bude zmazan.
                    Nsledne pokia mme ternrny a v vrchol (vrchol predstavujci v vzah) a s v
                    om zadefinovan nejak funkcie, ale nie s spojen vetky miesta, kde smeruj uzly, tak
                    v takom prpade ak by mal pri spojen vetkch uzov vznikn problm, e funkcie je
                    definovan viackrt pre dan
                    parametre, tak sa funkcia vymae zo zoznamu tohto vrcholu.
                </p>
            </Card.Body>
        </Card>
    </Collapse>
);

export default HelpGraphCollapse;